import { Grid } from '@mui/material'
import React from 'react'
import Details from './Details'
import Siren from './Siren'

function Chaine({data}) {
    const getColor = (data) => {
        if (data.AP > 0) return "darkblue";
        if (data.AM > 0) return "#c9c9c9";
        if (data.AQ > 0) return "darkred";
        if (data.AL > 0) return "gold";
        return "darkgreen";
    }
  return (
    <Grid sm={39} lg={20} md={20} xl={13} style={{position:'relative',marginTop:20}}>
        <div className="title">{data.name}</div>
        <div className='flexv2'>
            <Siren width={"25%"} color={getColor(data)} name=""/>
            <Details width={"74%"} data={data}/>

        </div>
    </Grid >
  )
}

export default Chaine